
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import recommend from '../../components/goods/recommend'
import share from '../../components/share/index'
export default {
directives: {
TransferDom
},
name: 'wash_card_detail',
components: {
Group,
Toast,
Scroller,
Cell,
Qrcode,
share,
Popup,
XButton,
Box,
recommend,
InlineLoading
},
data() {
return {
no: '',
name: '',
use_info: '',
extend_info: {},
x_now: 0,
x_str: "",
timer: null,
show_qrcode: false,
count: 0,
showNotice: false,
qrcodeLoding: true,
recommend_list: [{
id: '',
name: '闲时卡',
price: '10.00'
}, {
id: '',
name: '早鸟卡',
price: '10.00'
}]
}
},
activated: function() {
var self = this;
self.no = self.$route.params.id;

self.timer = setInterval(function() {
var timestamp = Date.parse(new Date()) / 1000;
if(self.x_now + 30 < timestamp) {
self.x_now = timestamp
self.fetchQrData()
}
}, 1000);
self.fetchData(self.no);
},

deactivated: function() {
clearInterval(this.timer)
},
methods: {
fetchData: function(id) {

var self = this;
self.xarpost('Mallvcard/getCardInfo', {
'id': id,
}).then(function(d) {
let {
data,
error,
message
} = d;
if(d.error != 0) {
self.$vux.toast.show({
text: d.message,
type: 'cancel',
time: 3000
});
}

var newData = (d.data == undefined) ? [] : d.data;
self.setData(newData);
}, function(e) {
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
});
},
setData(data) {
var self = this;
self.no = data.out_trade_no;
self.name = data.name;
self.use_info = data.card_use_info.msg;
self.extend_info = data.extend_info
},
fetchQrData: function() {
let self = this
self.qrcodeLoding = true;
self.xarpost('AllQrcode/getMyVcard', {
'out_trade_no': self.no
}).then(d => {
self.qrcodeLoding = false;
let {
data,
error,
message
} = d
if(error == 0) {
if(data) {
self.x_str = data.x_str;
self.x_now = data.x_now;
self.count = data.count;
self.show_qrcode = true
} else {
self.noticeBuy()
clearInterval(self.timer)
}
} else if(error == 10001) {
clearInterval(self.timer)
self.showtoast(message)
} else if(error == 12001) {
self.noticeBuy()
clearInterval(self.timer)
}
}, function(e){
self.qrcodeLoding = false;
self.$vux.toast.show({
text: e,
type: 'cancel',
time: 3000
});
})
},
showNoticePop: function() {
this.showNotice = true
},
giveFriend: function() {
let self = this
this.showNotice = false

if(this.count) {
self.xarpost('AllQrcode/giveVcard', {
'out_trade_no': self.no
}).then(d => {
let {
data,
error,
message
} = d
if(error == 0) {
self.setWxShare('我赠送了一张洗车卡。快去领取吧', '小矮人全城洗车服务上线。', 'http://xcxcgj.gotomore.cn/index.html#/giveVcard/' + data.card_code, 'http://xcxcgj.gotomore.cn/public/xarlogo.jpg');

self.$refs.share.showBox = true
} else {
self.showtoast(message)
}
})

} else {
this.noticeBuy('您没有卡可以赠送哦。现在带您去购买。')
}
},
noticeBuy: function(content = '您还没有可以使用的洗车卡，现在带您去购买。') {
let self = this
self.$vux.alert.show({
title: '小矮人',
content: content,
onHide() {
self.$router.push('/vcard/2')
}
})
}
}
}



